// Components
// the components colors may change depending of the version,
// options:
// 	onlinebanking - DACL-398 white and bright-blue
// 	default       - default theme
$components-version: 'default' !default;


//
// Grid
//
$break-small: 480px;
$break-medium: 600px;
$break-large-intermediate: 680px;
$break-large: 768px;
$break-xlarge: 1024px;
$break-wide: 1200px;
$max-width: 1020px;
$min-width: 320px;
$grid-padding: 20px;
$ie7-width: $max-width - $grid-padding * 2;


//
// Colors
//
$base: #000000 !default;
$white: #FFFFFF !default;
$pantone: #7c8796 !default;
$pale-gray: #f4f6f7 !default;
$mid-gray: #8996a0 !default;
$dark-gray: #666666 !default;
$warm-gray: #988f86 !default;
$lk-gray: #646d77 !default;
$dark-blue: #002244 !default;
$blue-gray: #3d4855 !default;
$amber: #e29317 !default;
$amber-2: #e8a945 !default;
$bright-blue: #0098DB !default;
$bright-blue-2: #33ade2 !default;
$gray: #8996a0 !default;
$positive: #7ab800 !default;
$negative: #e00034 !default;
$mid-gray-2: #a1abb3 !default;

//
// Social media colors
//
$facebook-color: #3b5998;
$twitter-color: #55acee;
$google-plus-color: #d34836;
$linkedin-color: #0077b5;
$xing-color: #126567;
$mail-color: $gray;

//
// Typography
//
$main-font: UniversDeutscheBank, Helvetica, Arial, sans-serif;
$basic-font: Arial, Helvetica, sans-serif;
$font-size-base: 14px;
$line-height-base: 22px;
$font-size-lead: 16px;
$line-height-lead: 25px;


//
// Global
//
$icons-path: '';
$body-bgc: $blue-gray !default;
$dark-bg: #293039 !default;
$bg-color: #010202 !default;
$flyout-box-bg: #1f2225 !default;
$internal-link-bgc: $amber !default;

//
// Background
//
$darkbg-primary-text: #98a2ad !default;
$darkbg-highlighted-text: #dedede !default;
$contentdarkbg: $mid-gray-2 !default;
$highlightedbg: $amber !default;
$content-hr: #cfd1d4;


//
// Darkbg
//
$dark-bg-elements-color: $white;


//
// Typography
//
$title-h1-color: $white !default;
$title-h2-color: $white !default;
$title-h3-color: $white !default;
$text-color: #4f5356 !default;
$text-primary: #787d82 !default;
$link-color: $amber !default;
$link-small-color: $amber-2 !default;
$item-highlight: $text-primary !default;


//
// hr
//
$hr: #c7c7c7 !default;
$hr-dark: #575a5e !default;
$hr-content: #cfd1d4 !default;


//
// Header
//
$header-slogan: $white !default;
$header-division-color: $white !default;
$header-group-nav-color: $white !default;
$header-language-navegation-hover: $amber !default;


//
// Footer
//
$footer-link-color: $white !default;
$footer-border-color: $hr-dark !default;
$footer-icon-feed-color: $white !default;
$footer-slogan-bgc: $white !default;


//
// Footnote
//
$footnote-text-color: $white !default;
$footnote-text-dark-color: $base !default;


// Navegation
$navegation-bg: #e7eaec !default;
$navegation-color: $base !default;
$navegation-icon-search-color: $amber !default;


//
// Menu
//
$main-menu-bg: #101213 !default;
$main-menu-vl: #1c1e1f !default;
$subline-bg: #98a2ad !default;
$main-menu-active: $amber !default;
$main-menu-arrow: $amber !default;
$main-menu-arrow-active: $white !default;


//
// Breadcrumb
//
$breadcrumb-item-last-color: $mid-gray-2 !default;
$breadcrumb-item-color: $white !default;
$breadcrumb-separator-color: $white !default;

$breadcrumb-options-print-icon-color: $white;
$breadcrumb-options-print-icon-color-hover: $amber;

$breadcrumb-options-share-icon-color: $white;
$breadcrumb-options-share-icon-color-hover: $amber;
$breadcrumb-options-share-icon-color-active-color: $white;
$breadcrumb-options-share-icon-color-active: $amber;
$breadcrumb-options-share-icon-color-active-item-hover: $amber;
$breadcrumb-options-share-icon-color-grey: #8996a0;


//
// Titles
//
$title-header-bc: $amber !default;
$title-header-color: #FFF !default;


//
// Teasers
//
$teasers-product-link-color: $white;

$color-cover-grey: $mid-gray-2 !default;
$color-cover-grey-hover: #b8c0c6 !default;
$color-cover-grey-dark: #4f5356 !default;
$color-cover-blue: #395880 !default;
$color-cover-blue-hover: #617999 !default;
$color-cover-black: $base !default;
$color-cover-black-hover: #333333 !default;
$color-cover-white: $pale-gray !default;
$color-cover-white-hover: $white !default;
$color-teaser-orange: $amber !default;
$color-teaser-orange-hover: $amber-2 !default;
$color-teaser-grey-bottom: $gray !default;
$color-teaser-grey-bottom-hover: $mid-gray-2 !default;
$color-teaser-blue-bottom: #617999 !default;
$color-teaser-blue-bottom-hover: #889bb3 !default;
$color-teaser-white-bottom: #e7eaec !default;
$color-teaser-white-bottom-hover: $pale-gray !default;

$teaser-bg-2-color: #f4f6f7;
$teaser-bg-3-title-color: $white;
$teaser-bg-3-color: $white;
$teaser-bg-3-detail-bgc: transparent;
$teaser-bg-3-detail-color: $amber;

$teaser-bg-4-title-color: $text-color;
$teaser-bg-4-color: $text-color;

// Teaser Stage
$teaser-stage-text-bgc: $color-cover-grey !default;
$teaser-stage-title-color: $white !default;
$teaser-stage-text-color: $color-cover-white !default;
$teaser-stage-cover-bgc: $color-cover-grey !default;

$teaser-decorator: $lk-gray !default;
$teasers-title-color: $color-cover-white !default;
