// Teasers Btn
$teaser-decorator-bgc: $color-teaser-orange !default;
$teaser-decorator-bgc-hover: $color-teaser-orange-hover !default;

// Teaser text
$teaser-text-color: $pale-gray !default;


//
// Slider
//
$slider-arrows-color: $amber;
$slider-arrows-color-active: $amber-2;


//
// Label
//
$label-1-bgc: $link-color !default;
$label-2-bgc: $gray !default;


//
// Subsnews
//
$subsnews-headline-color: $white !default;
$subsnews-bgc: #363e48 !default;
$subsnews-color: $white !default;
$subsnews-link-color: $white !default;
$subsnews-icon-color: $white !default;

//
// Own
//
$owl-bgc: #b9b9bb;
$owl-bgc-active: $highlightedbg !default;
$owl-outer-arrow-bgc: $white !default;
$owl-outer-arrow-bgc-hover: $amber !default;


//
// Button
//
$button-orange-bgc: $amber !default;
$button-orange-bgc-active: #f0b238 !default;

//
// Back link
//
$back-link-color: $white !default;

//
// Sticker
//
$sticker-bgc: $amber !default;
$sticker-bgc-hover: $amber-2 !default;
$sticker-divisor-bgc: $amber-2 !default;
$sticker-bc: $amber-2 !default;



//
// Tabs
//
$tabs-border-width: 5px !default;
$tabs-headline-color: $white !default;
$tabs-bc: $amber-2 !default;
$tabs-bc-active: $amber-2 !default;
$tabs-arrow-color: $pantone;
$tabs-arrow-bgc: $blue-gray;


//
// Others
//
$arrow-button-bg: #3d4855 !default;
$content-color: $pale-gray !default;
$teaser-color: $base !default;
$headings-color: $base !default;


//
// Paginator
//
$paginator-color: $white !default;
$paginator-color-active: $white !default;


//
// Icons
//
$icons-more-link-color: $white;
$icons-more-link-bgc: $amber;
$icons-close-badge-bgc: $amber;
$icons-apply-badge-bgc: $amber;

//
// Filters
//
$filter-row-bgc: $mid-gray-2;
$filter-option-active: $amber;
$filter-option-hover: #e8a945;
$filter-dropdown-active: $amber;
$filter-dropdown-hover: $amber;


//
// Notifications
//
$notification-success: $positive !default;
$notification-warning: $amber !default;
$notification-error: $negative !default;

//
// Table
//
$table-1-row-title: $base;
$table-1-row-description: $text-primary;
$table-1-header: #98a2ad !default;
$table-1-date-color: $text-color !default;
$table-1-row-odd: $content-color !default;
$table-1-row-even: #E6E9EB !default;
$table-economies-t2-color: $amber !default;
$table-togglable-internal-bgc: $blue-gray !default;



//
// Social Bar
//
$social-bar-mobile-bgc: $amber;


//
// Z-index
// allows us to at a glance determine relative layers of our application and prevents bugs
// arrising from arbitrary z-index values. Try not edit the z-index scale! Only add application
// scoped z-index values.
// reference: https://medium.com/@fat/mediums-css-is-actually-pretty-fucking-good-b8e2a6c78b06#.tvxq96xd8
$z-index-1: 100;
$z-index-2: 200;
$z-index-3: 300;
$z-index-4: 400;
$z-index-5: 500;
$z-index-6: 600;
$z-index-7: 700;
$z-index-8: 800;
$z-index-9: 900;
$z-index-10: 1000;

//
// Z-Index Applications
//
$z-index-10-sticky-header-detached: $z-index-10;
$z-index-10-progress-bar: $z-index-10;


