@import "core/variables";
@page {
      margin: 2cm;
 }

* {
	overflow: visible !important;
}

.hide-for-print {
	display: none !important;
}
%print-main-color {
	color:$base !important;
}
%print-main-bg {
	background:white !important;
}
%full-width {
	width: 100% !important;
}
%print-link {
	color: #0000F4 !important;
	text-decoration:underline !important;
}
.print-only {
	display: block !important;
}
a {
	@extend %print-link;
}
.mobile-only {
	height:auto !important;
	width:auto !important;
	overflow: auto !important;
	position:static!important;
}
/* ################### Global */
body {
	@extend %print-main-bg;
	font-family: $main-font;
 	font-size:11pt;
 }
h1, .h1,
.heading-labeled .heading-text-w p {
	font-size:16pt;
}
h2, .h2 {
	font-size:14pt;
}
 h3, .h3, .content-hl h3 {
 	font-size:14pt;
 }
h2, h3, .h2, .h3, .tags {
   page-break-after: avoid;
}
ul {
   page-break-inside: avoid;
}
table {
	margin-bottom: 18px;
	tr {
		border-bottom: 1px solid #adb5bd
	}
}

.main-footer {
	.quick-access {
		@extend .hide-for-print;
	}
	.contacts {
		@extend .hide-for-print;
	}
	.icons-darkbg-slogan {
		@extend .hide-for-print;
	}
	.icons-darkbg-logo {
		@extend .hide-for-print;
	}
	.list-inline.pipe-separator.text-medium {
		@extend .hide-for-print;
	}
	
}
input [type="placeholder"] {
	@extend .hide-for-print;
}
/* ################### Grid */
.grid12 [class*="col-"] {
    padding: 0 20px 0 0;
}
.grid12 .col-lg-12 {
	@extend %full-width;
	padding: 0 !important;
}
/* ################### Background-colors */
.contentdark-bg,
.contentlight-bg,
.subline,
.internal-concept,
.dark-bg {
	@extend %print-main-bg;
}
.dark-bg {
	p {
		@extend %print-main-color;
	}
}
.tags {
	.tag {
		@extend %print-main-bg;
		color:$base !important;
		border: 1px solid #b4bcc4;
		border-width: 0 0 0 1px;
		a {
			text-decoration: none;
		}
	}
	.tag:first-child {
		border-width: 0;
	}
}
.gradient-to-top,
.gradient {
	@extend .hide-for-print;
}
/* ################### Typografie */
h1,h2,h3,h4 {
	@extend %print-main-color;
}
a {
	@extend %print-link;
}
a.block-link {
	@extend %print-main-color;
	text-decoration:none !important;
}
.teaser-header {
	border-color: $base;
	page-break-after: avoid;
}
/* ################### Buttons/Links */
.icons-internal-link,
.icons-external-link{
	@extend .hide-for-print
}
.text-highlighted {
	@extend %print-main-color;
}

.icons-arrow-link {
	background: none;
	position:static;
	text-indent:0;
	width:auto;
	height:auto;
	display:inline;
}
a.download-teaser-link {
	padding-left:0 !important;
	@extend %print-link;
	span.download-data {
		@extend %print-link;
	}
	.icons-teaser-download-link {
		@extend .hide-for-print
	}
}

/* ################### States */
.js-text-toggle {
	.js-toggle, .hide-link {
		@extend .hide-for-print
	}
	.js-full-text {
		@extend .print-only 
	}
}
/* ################### Stickers */
.stickers {
	@extend .hide-for-print
}
/* ################### Header */
.main-header {
	border-bottom:5px solid $base;
	.col-xl-3.hide-md {
		@extend .hide-for-print
	}
	.distinction-set {
		@extend %print-main-color;
	}
}
/* ################### Navigation */
.main-menu-wrapper,
nav.actions {
	@extend .hide-for-print
}
.breadcrumb {
	a, span.separator {
		@extend %print-main-color;
	}
	li.active {
		span {
			color: lighten($base, 40%)
		}
	}
	
}
.icons-logo {
	@extend .hide-for-print
}
/* ################### Elements */
.video-player-wrapper {
	@extend .hide-for-print
}

.blockquote {
	color: $base;
	p {
		color: $base;
	}
	figure {
		display: none;
	}
	.icons-quote-start,
	.icons-quote-end {
		background: none !important;
		content: "&#8221;";
		text-indent: 0;
		display: inline !important;
		font-size:30px;
		top:auto;
		bottom:auto
	}
	.icons-quote-end {
		content: "&#8221;";
	}
}
.teaser-tab {
	@extend .hide-for-print;
}
.content-teaser {
	.el-box    {
		@extend .hide-for-print;
	}
	.text-box  {
		width:100% !important;
	}
}
/* ################### Slider */
.widget-teaser-wrap.js-slider {
	.teaser-item, .owl-item, .owl-wrapper {
		@extend %full-width;
		height: auto!important;
	}
	figure {
		@extend .hide-for-print;
	}
	.tags {
		position:static !important;
		margin: 0 0 12px 0  !important;
	}
	.teaser-item {
		padding:0;
		margin-bottom: 1cm;

		.icons-arrow-link {
			position:static;
		}
	}
	.control-wrapper {
		@extend .hide-for-print
	}
	.h2 {
		@extend %print-main-color;
		a {
			@extend %print-main-color;
		}
	}
	.teaser-top {
		margin:0  !important;
	}
	
	.no-fixed-link {
		padding-bottom:0;
		.teaser-footer {
			position:static;
		}
	}
	.full-pic {
		.teaser-inner {
		    background: none;
		    position: static;
		    padding: 0;
		}
	}
}
.product-link {
	@extend .hide-for-print
}
.tabs-wrapper,
.sticky-header-wrapper.detached.sticky-header-visible {
	@extend .hide-for-print
}

.symmetric-vert {
	overflow: visible;

	> [class*="col-"] {
		padding-bottom: 0 !important;
		margin-bottom: 0 !important;
	}
}

.internal-concept {
	.tabs {
		li {
			a {
				color:lighten($base, 50%)!important;
				text-decoration: none !important;
				border-color:#FFF;
			}
		}
		li.active {
			a {
				color:$base!important;
				border-color:$base;
			}
			
		}
	}
}
.filter-tags {
	li {
		a {
			color:lighten($base, 50%)!important;
			text-decoration: none !important;
			background:#FFF !important
		}
	}
	li.highlight {
		a {
			color:$base!important;
		}
	}
}
.chosen-dark {
	.chosen-container-single {
		.chosen-single {
			background: #FFF;
		}
	}
}
.ui-datepicker-trigger, .datepicker-action, .datepicker-arrow .icons-arrow-right-dark {
	@extend .hide-for-print	
}
.datepicker-arrow:after {
	content:'\25BA'
}
.performance-chart .datepicker-wrapper {
	display: inline;
	width:2.5cm;
	input {
		border:none;
		display: inline;
	
	}
}
.table-1 {
	tbody {
		
		a {
			text-decoration: none !important;
			span.row-title {
				text-decoration: underline;
			}
		}
	}
}

/* ################### Hompage */
.homepage-top {
	.homepage-top--item {
		background:#FFF;
		.teaser-inner *{
			color:#000;
		}
	}
	
	figure {
		@extend .hide-for-print
	}
	.control-wrapper {
		@extend .hide-for-print
	}
	.teaser-inner {
		position:static;
	}
	.owl-item {
		display: none;
		
	}
	.owl-item:first-child {
		display: block;
	}
	.mobile-only {
		display: block;
	}
}
.block-button {
	background:#FFF !important;
}
.product-finder-search {
	.icons-filled-search {
		@extend .hide-for-print
	}
}
.top-performer {
	.top-performer--table {
		a {
			p {
				@extend %print-link;
			}
		}
	}
	.combi-head a {
		@extend .hide-for-print
	}
}
.table-tips {
	@extend .hide-for-print
}